*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

body{
  font-family: 'Montserrat',sans-serif;
}

.banner{
  width: 100%;
  height: 100vh;
  background: url('img/temp.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  position:relative;
}

.banner_container {
  display: flex;
  align-items: center;
  height: 100vh;
  color:#D8D7C3;
}

.container{
  width: 1200px;
  max-width: 1200px;
  margin: 0 auto;
 
}

.banner_text {
  width: 500px;
}

.banner_text h3 {
  font-size: 40px;
  color:black;
  font-weight: 800;
}

.banner_text h1 {
  font-size: 80px;
  line-height: 80px;
  font-weight: 800;
  margin-top: 20px;
}

.banner_text p {
  font-size: 25px;
  font-weight: 400;
  margin-top: 20px;
}



.navbar {
  top:0;
  left: 0;
  width: 100%;
  height: 50px;
}

.logo {
  width: 40%;
  float: left;
  padding-left: 10%;
}

.logo img {
  width: 200px;
}

.links ul li a {
  text-decoration: none;
  color:rgb(0,0,0);
  font-weight: 800;
  font-size: 18px;
  text-shadow: 1px 1px 1px #D8D7C3;
  padding:0px 20px;
}

.links ul li a:hover {
  text-decoration: none;
  color:rgb(216, 215, 195);
  font-weight: 800;
  padding:0px 20px;
  text-shadow: 2px 2px 2px black;
}


.links ul li{
  float: left;
  list-style-type: none;
}

.links {
  width: 60%;
  float: right;
 

}


.about {
  padding: 80px ;
  width: 100%;
  display: flex;
}

.column-6 {
  width:50%;

}
 .column-3{
  width:50%;
 padding: 0px 30px;
 }

 .about__btn a {
  text-decoration: none;
  border: none;
  outline: none;
  color: rgba(0, 0, 0);
  font-weight: 700;
  border-radius: 30px;
  font-size: 12px; 
  padding: 20px 30px;
  background: #D8D7C3;
}

.about h3{
  color:rgba(37,37,37,0.7);
  font-size: 30px;
  margin-bottom: 12px;
  font-weight: 700;
}

.about h1{
  font-size:40px;
  color: #252525;
  font-weight: 700;
  line-height: 40px;
  margin-bottom: 15px;
}

.about p {
  font-size: 18px;
  columns: #8b8b8b;
  font-weight:300;
}

.about__btn{
  margin-top: 20px;
  overflow: hidden;
}

.about__img img{
  width: 100%;
  height: 350px;
  box-shadow: 10px 10px;
}




.shop {
  width: 100%;
  background: url("./img/temp.jpg");
  height: 450px;
  background-attachment: fixed;
  
}

.shop__btn a {
  text-decoration: none;
  border: none;
  outline: none;
  color: rgba(0, 0, 0);
  font-weight: 700;
  border-radius: 30px;
  font-size: 12px; 
  padding: 20px 30px;
  background: #D8D7C3;
}

.shop_text{
  height: 450px;
  display: flex;
  text-align: center;
  align-items: center;
  width: 400px;
  justify-content: center;
  margin: 0 auto;
  color: #D8D7C3;
}

.shop_text h1{
  font-size: 40px;
  line-height: 60px;
  font-weight: 600;
  font-style: normal;
  letter-spacing: 0.5px;
}

.shop__btn {
  margin-top: 35px;
}



.clients {
  width: 100%;
  padding: 40px 0;
  background: url("./img/temp.jpg");
  background-repeat: no-repeat;
  color: rgba(0, 0, 0);
  background-attachment: fixed;
}
.col-3{
  width: 25%;
}



.clients h1{
  text-align: center;
  font-weight: 800;
 font-size: 50px;
 padding-bottom: 20px;
 color:#D8D7C3;
}




.prices {
  width: 100%;
  margin-top: 20px;
  padding-bottom:30px;
}

.price:hover{
  opacity: 0.6;
  color:rgba(0, 0, 0);
}


.price{
  box-shadow: 10px 10px 5px grey;
  border-radius: 10px;
  color: rgba(32, 65, 104, 0.7);
  margin: 10px 0px 30px 0px;
  padding:10px 10px;
  background-color:#fff;
}


.price__img img{
  width:  90%;
  padding:0% 5%;
  height: auto;
}


.price__heading {
  margin-top: 14px;
  font-size: 25px;
}

.price__text {
  margin-top: 10px;
  color:rgba(37,37,37,0.7);
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-align: justify;
}


.price__btn a{
  text-decoration: none;
  border: none;
  outline: none;
  color: black;
  font-weight: 800;
  border-radius: 30px;
  font-size: 12px; 
  padding: 17px 30px;
  background:#D8D7C3;
}

.price__btn{
  margin-bottom: 10px;
  overflow: hidden;
}

.contents{
  width: 100%;
  height:500px;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-image: url("./img/download.jpg");
  font-weight: 800;
  color:rgba(37,37,37,0.7);
  font-size: 20px;
}

.contents h1{
  font-weight: 800;
 font-size: 70px;
 color:rgba(37,37,37,0.7);
}


.contents input{
  border-radius:10px;
  height:40px;
  opacity: 0.8;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.contents button{
  background-color:white;
  border-radius: 10px;
  margin-top: 20px;
  color:rgb(37,37,37);
  opacity: 0.5;
  border:1px rgba(0, 0, 0,0.5);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.5);
}

.contents button:hover{
  background-color: rgba(37,37,37,0.7);
  opacity: 0.8;
}



.roll h1{
  text-align: center;
  font-size: 80px;
  font-weight: 700;
  color:rgba(37,37,37,0.7)
}
.roll h3{
  text-align: center;
  font-size: 20px;
  color: black;
  font-weight: 500;
}

.pics{
width:100%;
padding: 20px 70px;
height: 700px;
}

.picleft{
  width: 55%;
  float: left;
  height:600px;
  padding:0px 6px;
}

.picleft img{
  width:100% ;
  height: 100%;
}

.picright{
  width: 45%;
  float: left;
  padding: 0px 5px ;
  height:100%;
}

#dem{
  margin-top: 10px;
}


.picright img{
  width:100%; 
  height:295px;
}

.footer{
  width: 100%;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-image: url("./img/temp.jpg");
}


.foot-left{
  width: 37%;
  float: left;
  padding: 0px 50px;
}

.foot-left p{
  color:#D8D7C3;
  font-size: 18px;
  font-weight: 600;
  text-align: justify;
}

.foot-left h3{
  font-size: 30px;
  color: #D8D7C3;
 font-weight: 800;
}

.foot-left img{
  width: 200px;
  padding-top:20px; 
  padding-bottom:20px
}

.foot-right{
  width: 63%;
  float: right;
  padding-top:30px;
}

.flink{
  width: 50%;
  height: 100%;
  float: left;
}

.flink h3{
  color: #D8D7C3;
  font-size: 30px;
  font-weight: 800;
}

#town{
  color:black;
}

.flink ul li a {
  text-decoration: none;
  color: #D8D7C3;
  font-size: 16px;
  font-weight: 500;
}

.flink ul li a:hover {
  text-decoration: none;
  color: #888888;
  font-weight: 800;
}


.flink ul li{
  list-style-type: none;
  margin-top: 10px;
}


.add{
  float: left;
  width: 50%;
  height: 100%;
}

.add p{
  font-size: 18px;
  font-weight: 600;
  text-align: justify;
  color: #D8D7C3;
}

.add h3{
  font-size: 30px;
  font-weight: 800;
}


